import React from 'react';
import { Link } from 'react-router-dom';

const BookTile = ({
  id,
  title,
  authorName,
  language,
  genre,
  url,
  slug,
  onDeleteClick
}) => {
  return(
    <tr>
      <td>
        <Link to={"/book/" + slug}>{title}</Link>
      </td>
      <td>{authorName}</td>
      <td>{language}</td>
      <td>{genre}</td>
      <td>
        <a
          className="btn btn-light"
          href={url}
        >
          View
        </a>
      </td>
      <td>
        <button
          className="btn btn-outline-danger"
          onClick={() => onDeleteClick(id)}
        >
          Delete
        </button>
      </td>
    </tr>
  )
}

export { BookTile };
