import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { loadBooks } from "../../../redux/actions/bookActions";

const _Book = ({
  books,
  loadBooks,
  ...props
}) => {
  const [book, setBook] = useState({ ...props.book });

  useEffect(() => {
    if (books.length === 0) {
      loadBooks().catch(error => {
        alert("Loading books failed" + error);
      });
    } else {
      setBook({ ...props.book });
    }
  }, [props.book]);

  return (
    <div>
      { book.id ? 
        <>
          <h2>{book.title}</h2>
          <p>{book.summary}</p>
          <h3>{book.authorName}</h3>
          <h3>{book.language}</h3>
          <h4>{book.genre}</h4>
          <a href={book.url}>Buy here</a>
        </> 
        :
        <h2>Sorry!!! We couldn't find your book.</h2>
      } 
    </div>
  );
}

export const getBookBySlug = (books, slug) => {
  return books.find(book => book.slug === slug) || null;
}

const mapStateToProps = (state, ownProps) => {
  const slug = ownProps.match.params.slug;
  const book =
    book && state.books.length > 0
      ? getBookBySlug(state.books, slug)
      : {};
  return {
    book,
    books: state.books
  };
}

const mapDispatchToProps = {
  loadBooks
};

export const Book = connect(mapStateToProps, mapDispatchToProps)(_Book);
